.formulario label {
    display: inline-block;
    vertical-align: top;
    width: 25%;
}

.formulario input, .formulario textarea {
    background: inline-block;
    vertical-align: top;
    width:25%;
}

.formulario input, .formulario textarea {
    background: #ddd;
    border-radius:5px;
    border:lpx solid #999;
    font-size: 0.8em;
    padding: 0.4em 0.6em;
    width:90%;
}

.formulario input [type="submit"]{
    background:#9da9ed;
    box-shadow: 0 0 10px rgba(0,0,0,0.4);
    color:white;
    font-size: lem;
    padding: 0.4em 0.2em;
    text-align: center;
    width: auto;
}

