nav {
    background-color: blue;
    margin-bottom: 2em;    
}

nav ul {
    list-style: none;
    margin: 0;
    padding:0;
}

nav ul li {
    display:inline-block;
    width:16.3%;
}

nav ul li a {
    color: white;
    text-decoration: none;
    text-align: center;
    padding: 0.2em 0.4em;
    display: block;
    position: relative;
}

nav ul li a:hover {
    background:rgba(0,0,0,0.3);
}

nav ul li a.activo {
    background: yellow;
}
