historia p {
    font-size: 0.8em;
}

.persona {
    width:18%;
    float: left;
    margin-left: 2.25%;
    border: 1px solid #ddd;
    border-radius: 0.25em;
    box-sizing: border-box;
}

.persona {
    text-align: center;
}

.persona img {
    border: radius 50%;
    box-shadow: 0 0 50px rgba(0,0,0,0.5);
    margin:0.8em 0 1em;
}

.persona h5, .persona h6 {
    font-weight: normal;
    color:#2e64a1;
    margin: 0;
}

.persona h5 {
    font-size: 0.9em;
}

.persona h6 {
    font-size: 0.8em;
    font-style: italic;
}

.perosna:first-child{
    margin-left:0;
}

